#home {
    .hero-section {
        background-color: var(--soft-gray);

        .hero-wrapper {
            display: flex;
            align-items: center;

            img {
                width: 400px;
                margin: 80px;
            }

            .text {
                width: 50%;
                margin-left: 50px;

                .title {
                    font-size: 50px;
                    color: var(--prime-color);
                    margin-bottom: 30px;
                }

                .description {
                    font-size: 25px;
                }
            }
        }
    }

    .about-us {
        .about-us-wrapper {
            display: flex;
            align-items: center;

            img {
                width: 400px;
                margin: 80px;
            }

            .text {
                width: 50%;
                margin-left: 50px;

                .title {
                    font-size: 40px;
                    color: var(--dark-gray);
                    margin-bottom: 5px;
                }

                .line {
                    display: block;
                    height: 3px;
                    width: 300px;
                    border-bottom: 3px;
                    background-color: var(--prime-color);
                }

                .sub-title {
                    font-size: 20px;
                    font-weight: 700;
                    margin: 30px 0 20px 0;
                }

                .description {
                    line-height: 20px;
                }
            }
        }
    }

    .products {
        .products-wrapper {
            .list {
                display: flex;
                justify-content: space-between;
                margin: 30px 0 50px 0;

                .item {
                    a {
                        color: #000000;
                        cursor: pointer;
                    }

                    img {
                        width: 350px;
                        height: 250px;
                        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
                        margin-bottom: 10px;
                        cursor: pointer;
                    }

                    p {
                        text-align: center;
                        font-weight: 600;
                        font-size: 18px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}